.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h4 {
  margin-top: 0;
  font-size: 14px;
  color: #000;
}

.main-container {
  min-width: 100%;
}

.list-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-width: 100%;
  flex-wrap: wrap;
}

.player {
  flex-grow: 1;
  margin: 10px;
  padding-top: 10px;
  background: #f0f3f4;
  border-radius: 10px;
}

.basic-single {
  flex-grow: 1;
  margin: 10px;
}
